export default "<span class=\"hljs-keyword\">import</span> { Component, ChangeDetectionStrategy, Injectable } from <span class=\"hljs-string\">'@angular/core'</span>;\n<span class=\"hljs-keyword\">import</span> { CalendarEvent, CalendarUtils } from <span class=\"hljs-string\">'angular-calendar'</span>;\n<span class=\"hljs-keyword\">import</span> { subWeeks, startOfMonth, endOfMonth, addWeeks } from <span class=\"hljs-string\">'date-fns'</span>;\n<span class=\"hljs-keyword\">import</span> { GetMonthViewArgs, MonthView } from <span class=\"hljs-string\">'calendar-utils'</span>;\n\n@Injectable()\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> MyCalendarUtils extends CalendarUtils {\n  getMonthView(args: GetMonthViewArgs): MonthView {\n    args.viewStart = subWeeks(startOfMonth(args.viewDate), <span class=\"hljs-number\">1</span>);\n    args.viewEnd = addWeeks(endOfMonth(args.viewDate), <span class=\"hljs-number\">1</span>);\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-keyword\">super</span>.getMonthView(args);\n  }\n}\n\n@Component({\n  selector: <span class=\"hljs-string\">'mwl-demo-component'</span>,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n  templateUrl: <span class=\"hljs-string\">'template.html'</span>,\n  providers: [\n    {\n      provide: CalendarUtils,\n      useClass: MyCalendarUtils,\n    },\n  ],\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> DemoComponent {\n  viewDate: <span class=\"hljs-built_in\">Date</span> = <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Date</span>();\n\n  events: CalendarEvent[] = [];\n}\n";